            log.debug('Found a alias in lieu of model: ' + JSON.stringify(aliasData));
            modelData = this.data.find((model) => model.provider === aliasData.provider && model.name === aliasData.alias);
            log.debug('Found relevant model:  ' + JSON.stringify(modelData));
